.cbp-af-header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	background: $col-black;
	z-index: 10000;
	height: 230px;
	overflow: hidden;
	-webkit-transition: height 0.3s;
	-moz-transition: height 0.3s;
	transition: height 0.3s;
}

.cbp-af-header .cbp-af-inner {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	//max-width: 69em;
	margin: 0 auto;
	margin-left: 300px;
	padding: 2.875em 1.875em 1.875em;
}

.cbp-af-header h1,
.cbp-af-header nav {
	display: inline-block;
	position: relative;
}

.cbp-af-header nav  {
	justify-content: flex-end;
}

.cbp-af-header {
	display: flex;
	align-items: center;

    .cbp-af-logo {
		display: flex;
		align-items: center;
		justify-content: space-between;
        max-width: 320px;
        font-family: 'Courier Prime', monospace;
        font-family: 'Share Tech Mono', monospace;

        h1 {
            text-transform: uppercase;
            color: $col-white;
            letter-spacing: 4px;
            font-size: 4em;
            font-weight: 600;
            margin: 0;

            &::after {
                content: '';
                display: inline-block;
                width: 1px;
                height: 45px;
                background-color: #FFFFFF;
                margin-right: 10px;
            }
        }

        h2 {
            font-size: 1em;
            font-weight: normal;
            color: $col-white;
            letter-spacing: 0.1em;
            line-height: 20px;
        }
    }
}

.cbp-af-header nav {
	//float: right;
}

.cbp-af-header nav a {
    font-family: 'Courier Prime', monospace;
    font-family: 'Share Tech Mono', monospace;
	color: $col-white;
	margin: 0 0 0 20px;
    font-size: $font-size-base;
	letter-spacing: 0.1em;
	text-transform: uppercase;
}

.cbp-af-header nav a:hover {
	color: #333;
}

/* Transitions and class for reduced height */
.cbp-af-header h1,
.cbp-af-header nav a {
	-webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
	transition: all 0.3s;
}

.cbp-af-header.cbp-af-header-shrink {
	height: 90px;
}

/* Example Media Queries */
@media screen and (max-width: 55em) {

	.cbp-af-header .cbp-af-inner {
		width: 100%;
	}

	.cbp-af-header h1,
	.cbp-af-header nav {
		display: block;
		margin: 0 auto;
		text-align: center;
		float: none;
	}

	.cbp-af-header h1,
	.cbp-af-header nav a {
		line-height: 115px;
	}

	.cbp-af-header nav a {
		margin: 0 10px;
	}

	.cbp-af-header.cbp-af-header-shrink h1,
	.cbp-af-header.cbp-af-header-shrink nav a {
		line-height: 45px;
	}

	.cbp-af-header.cbp-af-header-shrink h1 {
		font-size: 2em;
	}

	.cbp-af-header.cbp-af-header-shrink nav a {
		font-size: 1em;
	}
}

@media screen and (max-width: 32.25em) {
	.cbp-af-header nav a {
		font-size: 1em;
	}
}

@media screen and (max-width: 24em) {
	.cbp-af-header nav a,
	.cbp-af-header.cbp-af-header-shrink nav a {
		line-height: 1;
	}
}
